.switch {
	position: relative;
	display: inline-block;
	width: 40px;
	height: 10px;
}

.switch input {
	opacity: 0;
	width: 0;
	height: 0;
}

.slider {
	position: absolute;
	cursor: pointer;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: #ccc;
	-webkit-transition: .4s;
	transition: .4s;
}

.slider::before {
	position: absolute;
	content: "";
	height: 22px;
	width: 22px;
	left: 0;
	bottom: -5px;
	background-color: #B3B3B3;
	-webkit-transition: .4s;
	transition: .4s;
}

input:checked+.slider {
	background-color: rgba(0, 176, 255, 0.53);
}

input:focus+.slider {
	box-shadow: 0 0 1px #2196F3;
}

input:checked+.slider:before {
	background-color: #00B0FF;
	-webkit-transform: translateX(25px);
	-ms-transform: translateX(25px);
	transform: translateX(25px);
}

.slider.round {
	border-radius: 34px;
}

.slider.round:before {
	border-radius: 50%;
}

